import {listNhac, tiengChuong} from '@/components/javascript/listNhac'
import {ElMessage, ElMessageBox} from 'element-plus'

export let audio

export function playNhac(change, index, isNhac) {
  if (!audio || audio.ended || change) {
    pauseNhac()
    const list = isNhac ? listNhac : tiengChuong
    if(index === -1) index = Math.round(Math.random() * (isNhac ? 13 : 2))
    audio = new Audio(list[index].source)
    audio.play().then(() => {
      // do nothing
    })
  } else {
    startNhac()
  }
}

export let trongThoiHan

export function phatNhacTrong(seconds, index, isNhac) {
  clearInterval(trongThoiHan)
  trongThoiHan = null
  if (!audio || audio.ended || audio.paused) {
    playNhac(true, index, isNhac)
    trongThoiHan = setInterval(() => {
      seconds--
      if (audio.ended) {
        phatNhacTrong(seconds, index, isNhac)
        // playNhac(true, index, isNhac)
      }
      if (seconds < 1) {
        pauseNhac()
        clearInterval(trongThoiHan)
      }
    }, 1000)
  }
}

export let voThoiHan

export function phatNhacVoThoiHan(index) {
  clearInterval(voThoiHan)
  voThoiHan = null
  if (!audio || audio.ended || audio.paused) {
    playNhac(false, index, true)
    voThoiHan = setInterval(() => {
      if (audio.ended) {
        phatNhacVoThoiHan(index)
      }
    }, 500)
  }
}

export function pauseNhac() {
  if (!!audio && !audio.ended) {
    audio.pause()
  }
  clearInterval(voThoiHan)
  clearInterval(trongThoiHan)
  voThoiHan = null
  trongThoiHan = null
}

export function startNhac() {
  if (!!audio && !audio.ended) {
    audio.play()
  }
}

export function clearNhac() {
  pauseNhac()
  audio = null
}

export const SETTING_APP = 'SETTING_APP'

export const SETTING_DEFAULT = {
  workTime: 1500,
  breakTime: 300,
  longBreakTime: 1200,
  isLongBreak: true,
  continuous: true,
  workCount: 4,
  musicInBreak: false,
  // nhac
  endWork: {
    indexNhac: -1,
    isNhac: true,
    limitTimeNhac: -1
  },
  endBreak: {
    indexNhac: -1,
    isNhac: true,
    limitTimeNhac: -1
  }
}

export const ERROR = 'error'
export const SUCCESS = 'success'
export const WARNING = 'warning'
export const DANGER = 'danger'

export async function showConfirm(title, dongY = () => {
}, tuChoi = () => {
}, status = WARNING) {
  ElMessageBox.confirm(
    title,
    'XÁC NHẬN',
    {
      confirmButtonText: 'Đồng ý',
      cancelButtonText: 'Từ chối',
      type: status
    }
  )
    .then(() => {
      dongY()
    })
    .catch(() => {
      tuChoi()
    })
}

export function showAlert(content, type = SUCCESS) {
  ElMessage({
    message: content,
    type: type,
  })
}

export function requiredRule(label, trigger = ['change', 'blur']) {
  return { required: true, message: label + ' là bắt buộc', trigger: trigger}
}
