<template>
  <div class="hello">
    <el-image v-if="breaking" style="width: 150px;" :src="meme.relax"/>
    <el-image v-if="working" style="width: 150px;" :src="meme.work"/>
    <div class="demo-progress">
      <el-progress type="circle" :percentage="percent" :status="percent <= 0 ? 'success' : ''" width="240"
                   :stroke-width="12"/>
    </div>
    <div style="font-size: 60px; font-family: 'Roboto', sans-serif;">{{ secondFormat }}</div>
    <el-row :gutter="gutter" style="justify-content: center; margin: 20px auto">
      <el-button type="primary" :icon="SwitchButton" circle title="Làm" @click="lam" size="large"/>
      <el-button v-if="status === STATUS.PAUSED" type="success" :icon="VideoPlay" circle title="Tiếp tục"
                 @click="chayOrDung(true, true)" size="large"/>
      <el-button v-if="status === STATUS.PLAYED" type="warning" :icon="VideoPause" circle title="Tạm dừng"
                 @click="chayOrDung(false, true)" size="large"/>
      <el-button type="info" :icon="CoffeeCup" circle title="Nghỉ" @click="nghi" size="large"/>
      <el-button type="danger" :icon="Refresh" circle title="Reset" @click="reset(false)" size="large"/>
    </el-row>
    <div style="justify-content: center; width: 300px; margin: 0 auto; height: 50px">
      <el-icon v-for="(item) of listPhien" :key="item" style="width: 50px" :size="getSizeClock(item)">
        <AlarmClock/>
      </el-icon>
    </div>
    <el-button
        type="primary"
        @click="openDialog"
    >
      Cài đặt
    </el-button>
    <el-button
        v-if="false"
        type="primary"
        @click="baoHetGio"
    >
      Phát nhạc
    </el-button>
    <popup-setting
        v-if="showDialog"
        v-model:show-dialog="showDialog"
        v-model:setting="setting"
        @changeSetting="reset(true)"
    />
    <video v-show="false" id="keepVideo">
      <source src="data:video/mp4;base64, ">
      <source src="data:video/ogg;base64, " type="video/ogg">
      <source src="data:video/webm;base64, " type="video/webm">
    </video>
    <el-button  v-show="keepButton" @click="checkVideo">Giữ màn hình sáng</el-button>
  </div>
</template>

<script>
import {
  audio, clearNhac, pauseNhac,
  phatNhacTrong, phatNhacVoThoiHan,
  playNhac,
  SETTING_APP,
  SETTING_DEFAULT,
  showConfirm,
} from "@/components/javascript/mainFunction";
import {cloneDeep} from "lodash";
import {CoffeeCup, Refresh, SwitchButton, VideoPause, VideoPlay} from "@element-plus/icons-vue";
import {meme} from "@/components/javascript/listNhac";
import PopupSetting from "@/components/PopupSetting";

const STATUS = {
  HIDE: 1,
  PAUSED: 2,
  PLAYED: 3
}

export default {
  components: {PopupSetting},
  filters: {},
  data() {
    return {
      seconds: 0,
      phien: null, // lẻ là phiên làm, chẵn là phiên nghỉ
      setting: cloneDeep(SETTING_DEFAULT),
      status: STATUS.HIDE,
      gutter: 10,
      demNguoc: undefined,
      showDialog: false,
      CoffeeCup,
      VideoPause,
      VideoPlay,
      SwitchButton,
      Refresh,
      STATUS,
      meme,
      video: null,
      keepButton: true
    }
  },
  computed: {
    breaking() {
      return (!!this.phien || this.phien === 0) && this.phien % 2 === 0
    },
    working() {
      return !!this.phien && this.phien % 2 === 1
    },
    secondFormat() {
      const minute = Math.floor(this.seconds / 60)
      const second = this.seconds % 60
      return minute + ':' + second
    },
    percent() {
      const total = this.phien % 2 === 0 ? this.setting.breakTime : this.setting.workTime
      return Math.round((this.seconds / total) * 100)
    },
    listPhien() {
      const list = []
      for (let i = 0; i <= this.phien; i++) {
        if (i % 2 === 1) {
          list.push(i)
        }
      }
      return list
    },
    sizeClock() {
      return ((this.seconds % 3) + 1) * 10
    }
  },
  created() {
    this.initSetting()
  },
  mounted() {
    if (!this.video) {
      this.video = document.getElementById("keepVideo")
      this.video.loop = true
      this.video.load()
    }
  },
  watch: {},
  methods: {
    checkVideo() {
      this.video.play()
      this.keepButton = false
      alert(this.video.loop + ' - ' + this.video.paused)
    },
    openDialog() {
      this.showDialog = true
    },
    getSizeClock(item) {
      if (item === this.phien) {
        return this.sizeClock
      } else {
        return 30
      }
    },
    initSetting() {
      this.setting = JSON.parse(localStorage.getItem(SETTING_APP))
      if (!this.setting) {
        this.setting = cloneDeep(SETTING_DEFAULT)
        localStorage.setItem(SETTING_APP, JSON.stringify(SETTING_DEFAULT))
      }
    },
    lam() {
      const action = () => {
        this.seconds = this.setting.workTime
        if (!this.phien) {
          this.phien = 1
        } else if (this.phien % 2 === 0) {
          this.phien++
        }
        this.chayOrDung(true)
      }
      if (this.seconds > 0) {
        showConfirm('Bạn có muốn phiên làm việc mới?', () => {
          action()
        })
      } else {
        action()
      }
    },
    chayOrDung(isRun, isPhatNhac = false) {
      clearInterval(this.demNguoc)
      this.status = isRun ? STATUS.PLAYED : STATUS.PAUSED
      if (isRun && this.seconds > 0) {
        this.demNguoc = setInterval(() => {
          this.seconds--
          if (this.seconds <= 0) {
            clearInterval(this.demNguoc)
            this.status = STATUS.HIDE
            this.baoHetGio()
            if (this.setting.continuous) {
              if (this.breaking) {
                this.lam()
                return
              }
              if (this.working) {
                this.nghi()
              }
            }
          }
        }, 1000)
        // luc nghi: khi click start neu co nhac dang hat thi tiep tuc
        if (this.breaking && this.setting.musicInBreak && isPhatNhac) {
          phatNhacVoThoiHan(-1)
        }
      } else {
        if (this.seconds > 0) {
          this.status = STATUS.PAUSED
          // luc nghi: khi click paused neu co nhac dang hat thi dung lai
          if (this.breaking && this.setting.musicInBreak && isPhatNhac) {
            pauseNhac()
          }
        }
      }
    },
    baoHetGio() {
      // neu la luc nghi co nhac thi dừng nhạc lại
      if (this.breaking && this.setting.musicInBreak && !!audio && !audio.ended && !audio.paused) {
        pauseNhac()
        return
      }
      const setting = this.working ? this.setting.endWork : this.setting.endBreak
      if (setting.limitTimeNhac < 0) {
        playNhac(true, setting.indexNhac, setting.isNhac)
      } else {
        phatNhacTrong(setting.limitTimeNhac, setting.indexNhac, setting.isNhac)
      }
    },
    nghi() {
      const action = () => {
        if ((this.listPhien.length > 0 && this.listPhien.length % this.setting.workCount === 0) && this.setting.isLongBreak) {
          if (this.setting.continuous) {
            this.seconds = this.setting.longBreakTime
            this.phien = 0
          } else {
            showConfirm(`Bạn có muốn nghỉ ngơi dài kết thúc ${this.setting.workCount} phiên làm việc?`, () => {
              this.seconds = this.setting.longBreakTime
              this.phien = 0
            }, () => {
              this.seconds = this.setting.breakTime
              if (!this.phien) {
                this.phien = 0
              } else if (this.phien % 2 === 1) {
                this.phien++
              }
            })
          }
        } else {
          this.seconds = this.setting.breakTime
          if (!this.phien) {
            this.phien = 0
          } else if (this.phien % 2 === 1) {
            this.phien++
          }
        }
        this.chayOrDung(true)
        // phat nhac trong luc nghi (neu co)
        if (this.setting.musicInBreak) {
          const play = setInterval(() => {
            if (!audio || audio.ended || audio.paused) {
              phatNhacVoThoiHan(-1)
              clearInterval(play)
            }
          }, 500)
        }
      }
      if (this.seconds > 0) {
        showConfirm('Bạn có muốn phiên nghỉ ngơi mới?', () => {
          action()
        })
      } else {
        action()
      }
    },
    reset(check = false) {
      const action = () => {
        clearInterval(this.demNguoc)
        this.initSetting()
        this.seconds = 0
        this.phien = null
        this.status = STATUS.HIDE
        this.demNguoc = undefined
        clearNhac()
      }
      if (check) {
        action()
      } else {
        showConfirm('Bạn có muốn reset?', () => {
          action()
        })
      }
    }
  }
}
</script>

