<template>
  <el-dialog
      :destroy-on-close="true"
      title="CÀI ĐẶT"
      v-model="showDialogSync"
      :close-on-click-modal="false"
      :width="width"
      :before-close="onClose"
      top="3%"
  >
    <el-card>
      <template #header>
        Cài đặt pomodoro
      </template>
      <el-form
          ref="refFormSetting"
          :model="formSetting"
          :rules="rulesFormSetting"
          :label-width="labelWidth"
      >
        <el-row :gutter="gutter">
          <el-col :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-form-item
                label="Số phút làm việc"
                prop="workMinute"
            >
              <el-input
                  v-model="formSetting.workMinute"
                  type="number"
                  min="1"
                  max="500"
                  placeholder="Số phút làm việc"
              />
            </el-form-item>
          </el-col>
          <el-col :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-form-item
                label="Số phút nghỉ ngơi"
                prop="breakMinute"
            >
              <el-input
                  v-model="formSetting.breakMinute"
                  type="number"
                  min="1"
                  max="500"
                  placeholder="Số phút nghỉ ngơi"
              />
            </el-form-item>
          </el-col>
          <el-col :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-form-item
                label="Số phút nghỉ dài"
                prop="longBreakMinute"
            >
              <el-input
                  v-model="formSetting.longBreakMinute"
                  type="number"
                  min="1"
                  max="500"
                  placeholder="Số phút nghỉ dài"
              />
            </el-form-item>
          </el-col>
          <el-col :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-form-item
                label="Có nghỉ dài"
                prop="isLongBreak"
            >
              <el-switch v-model="formSetting.isLongBreak"/>
            </el-form-item>
          </el-col>
          <el-col :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-form-item
                label="Số phiên làm việc"
                prop="workCount"
            >
              <el-input
                  v-model="formSetting.workCount"
                  type="number"
                  min="1"
                  max="500"
                  placeholder="Số phiên làm việc"
              />
            </el-form-item>
          </el-col>
          <el-col :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-form-item
                label="Chế độ liên tục"
                prop="continuous"
            >
              <el-switch v-model="formSetting.continuous"/>
            </el-form-item>
          </el-col>
          <el-col :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-form-item
                label="Nghe nhạc trong lúc nghỉ"
                prop="musicInBreak"
            >
              <el-switch v-model="formSetting.musicInBreak"/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-button
            @click="onResetFormSetting"
            type="primary"
        >
          Đặt về mặc định
        </el-button>
      </el-form>
    </el-card>
    <el-card style="margin-top: 10px">
      <template #header>
        <span>Cài đặt nhạc</span>
      </template>
      <el-form
          ref="refFormValues"
          :model="formValues"
          :label-width="labelWidth"
      >
        <el-row :gutter="gutter">
          <el-col :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-form-item
                label="Loại làm/nghỉ"
                prop="loai"
            >
              <el-radio-group v-model="formValues.loai">
                <el-radio :label="LOAI.LAM">Nhạc kết thúc làm</el-radio>
                <el-radio :label="LOAI.NGHI">Nhạc kết thúc nghỉ</el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-form-item
                label="Nhạc/chuông"
                prop="nhacOrChuong"
            >
              <el-radio-group v-model="formValues.nhacOrChuong">
                <el-radio :label="NHAC_OR_CHUONG.NHAC">Nhạc</el-radio>
                <el-radio :label="NHAC_OR_CHUONG.CHUONG">Chuông</el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-row>
              <el-col :span="20">
                <el-form-item
                    label="Chọn bài"
                    prop="idNhac"
                >
                  <el-select
                      v-model="formValues.idNhac"
                      placeholder="Chọn bài"
                      :clearable="false"
                      default-first-option
                      style="width: 100%"
                  >
                    <el-option
                        label="Ngẫu nhiên"
                        :value="-1"
                    />
                    <el-option
                        v-for="(item, index) in listNhacChuong"
                        :key="index"
                        :label="item.name"
                        :value="index"
                    />
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="4">
                <el-button type="primary" :icon="Service" circle @click="ngheThu">
                </el-button>
              </el-col>
            </el-row>
          </el-col>
          <el-col :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-form-item
                label="Giới hạn phát"
                prop="isLimitNhac"
            >
              <el-switch v-model="formValues.isLimitNhac"/>
            </el-form-item>
          </el-col>
          <el-col v-if="formValues.isLimitNhac" :sm="24" :xs="24" :md="24" :lg="12" :xl="12">
            <el-form-item
                label="Số giây"
                prop="limitSecond"
            >
              <el-slider v-model="formValues.limitSecond" :min="3"/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-button
            @click="onResetFormValues"
            type="primary"
        >
          Đặt về mặc định
        </el-button>
      </el-form>
    </el-card>
    <el-button
        @click="onSave"
        type="success"
        style="margin-top: 10px"
    >
      Lưu
    </el-button>
  </el-dialog>
</template>

<script>
import {
  pauseNhac,
  playNhac,
  requiredRule,
  SETTING_APP,
  showAlert,
  showConfirm
} from '@/components/javascript/mainFunction'
import {listNhac, tiengChuong} from "@/components/javascript/listNhac"
import {Service} from "@element-plus/icons-vue"
import {cloneDeep} from "lodash";

const LOAI = {
  LAM: 1,
  NGHI: 2
}

const NHAC_OR_CHUONG = {
  NHAC: 1,
  CHUONG: 2
}

const FORM_VALUE_DEFAULT = {
  loai: LOAI.LAM,
  nhacOrChuong: NHAC_OR_CHUONG.NHAC,
  idNhac: -1,
  isLimitNhac: false,
  limitSecond: 10
}

const FORM_SETTING_DEFAULT = {
  workMinute: 25,
  breakMinute: 5,
  longBreakMinute: 20,
  isLongBreak: true,
  workCount: 4,
  continuous: true,
  musicInBreak: false
}

export default {
  props: {
    setting: {
      type: Object,
      required: true
    },
    showDialog: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      isNgheThu: false,
      LOAI,
      Service,
      gutter: 10,
      formValues: cloneDeep(FORM_VALUE_DEFAULT),
      NHAC_OR_CHUONG,
      formSetting: cloneDeep(FORM_SETTING_DEFAULT),
      rulesFormSetting: {
        workMinute: [requiredRule('Số phút làm việc')],
        breakMinute: [requiredRule('Số phút nghỉ ngơi')],
        longBreakMinute: [requiredRule('Số phút nghỉ dài')],
        workCount: [requiredRule('Số phiên làm việc')]
      }
    }
  },
  computed: {
    width() {
      return window.innerWidth > 680 ? '80%' : '100%'
    },
    labelWidth() {
      return window.innerWidth > 680 ? '190px' : '150px'
    },
    listNhacChuong() {
      return this.formValues.nhacOrChuong === NHAC_OR_CHUONG.CHUONG ? tiengChuong : listNhac
    },
    settingSync: {
      get() {
        return this.setting
      },
      set(val) {
        this.$emit('update:setting', val)
      }
    },
    showDialogSync: {
      get() {
        return this.showDialog
      },
      set(val) {
        this.$emit('update:showDialog', val)
      }
    }
  },
  watch: {
    'formValues.loai': {
      immediate: true,
      handler: function (val) {
        if (val === LOAI.NGHI) {
          this.formValues.nhacOrChuong = this.settingSync.endBreak.isNhac ? NHAC_OR_CHUONG.NHAC : NHAC_OR_CHUONG.CHUONG
          this.formValues.idNhac = this.settingSync.endBreak.indexNhac
          this.formValues.isLimitNhac = (this.settingSync.endBreak.limitTimeNhac > 0)
          this.formValues.limitSecond = this.settingSync.endBreak.limitTimeNhac
        } else if (val === LOAI.LAM) {
          this.formValues.nhacOrChuong = this.settingSync.endWork.isNhac ? NHAC_OR_CHUONG.NHAC : NHAC_OR_CHUONG.CHUONG
          this.formValues.idNhac = this.settingSync.endWork.indexNhac
          this.formValues.isLimitNhac = (this.settingSync.endWork.limitTimeNhac > 0)
          this.formValues.limitSecond = this.settingSync.endWork.limitTimeNhac
        }
      }
    }
  },
  mounted() {
    this.initForm()
  },
  methods: {
    ngheThu() {
      this.isNgheThu = true
      playNhac(true, this.formValues.idNhac, this.formValues.nhacOrChuong === NHAC_OR_CHUONG.NHAC)
    },
    initForm() {
      this.formValues.nhacOrChuong = this.settingSync.endWork.isNhac ? NHAC_OR_CHUONG.NHAC : NHAC_OR_CHUONG.CHUONG
      this.formValues.idNhac = this.settingSync.endWork.indexNhac
      this.formValues.isLimitNhac = (this.settingSync.endWork.limitTimeNhac > 0)
      this.formValues.limitSecond = this.settingSync.endWork.limitTimeNhac

      this.formSetting.workMinute = this.settingSync.workTime / 60
      this.formSetting.breakMinute = this.settingSync.breakTime / 60
      this.formSetting.longBreakMinute = this.settingSync.longBreakTime / 60
      this.formSetting.isLongBreak = this.settingSync.isLongBreak
      this.formSetting.workCount = this.settingSync.workCount
      this.formSetting.continuous = this.settingSync.continuous
      this.formSetting.musicInBreak = this.settingSync.musicInBreak
    },
    onResetFormValues() {
      showConfirm('Bạn có muốn đặt nhạc về mặc định?', () => {
        this.$refs.refFormValues.resetFields()
        this.formValues = cloneDeep(FORM_VALUE_DEFAULT)
      })
    },
    onResetFormSetting() {
      showConfirm('Bạn có muốn đặt setting về mặc định?', () => {
        this.$refs.refFormSetting.resetFields()
        this.formSetting = cloneDeep(FORM_SETTING_DEFAULT)
      })
    },
    onSaveSetting() {
      this.settingSync.workTime = this.formSetting.workMinute * 60
      this.settingSync.breakTime = this.formSetting.breakMinute * 60
      this.settingSync.longBreakTime = this.formSetting.longBreakMinute * 60
      this.settingSync.isLongBreak = this.formSetting.isLongBreak
      this.settingSync.workCount = this.formSetting.workCount
      this.settingSync.continuous = this.formSetting.continuous
      this.settingSync.musicInBreak = this.formSetting.musicInBreak
      localStorage.setItem(SETTING_APP, JSON.stringify(this.settingSync))
    },
    onSaveNhac() {
      const changeObj = (obj) => {
        obj.isNhac = this.formValues.nhacOrChuong === NHAC_OR_CHUONG.NHAC
        obj.indexNhac = this.formValues.idNhac
        if (!this.formValues.isLimitNhac) {
          obj.limitTimeNhac = -1
        } else {
          obj.limitTimeNhac = this.formValues.limitSecond
        }
      }
      if (this.formValues.loai === LOAI.NGHI) {
        changeObj(this.settingSync.endBreak)
      } else {
        changeObj(this.settingSync.endWork)
      }
      localStorage.setItem(SETTING_APP, JSON.stringify(this.settingSync))
    },
    onClose() {
      this.showDialogSync = false
      if (this.isNgheThu) {
        pauseNhac()
      }
    },
    onSave() {
      this.onSaveNhac()
      this.onSaveSetting()
      showAlert('Lưu thành công')
      this.$emit('changeSetting')
      this.onClose()
    }
  }
}
</script>

<style>
.el-form-item__label {
  text-align: left !important;
}
</style>
